<template>
<!-- -->
  <div class="myzone">
    <mt-header fixed title=""></mt-header>
    <div class="myzoneContent">
      <div class="headpicbox bgwhite">
        <div class="headpic">
          <div class="imgbox">
            <img src="http://p1.music.126.net/OiWRTvuOeiXRZNWKzvzRWQ==/18503681184549091.jpg?param=80y80" alt="">
          </div>
          <div class="info">
            <em></em>
            <i>Lv.5</i>
          </div>
          <button type="buttom" :disabled="isCheckin" @click="checkin()">{{checkInMsg}}</button>
        </div>

        <div class="myzonenav">

          <a href="javascript:;">
            <p class="top"></p>
            <em>0</em>
          </a>
          <a href="javascript:;">
            <p class="top"></p>
            <em>10</em>
          </a>
          <a href="javascript:;">
            <p class="top"></p>
            <em>0</em>
          </a>
          <a href="javascript:;">
            <i class="mui-icon mui-icon-compose"></i>
            <p></p>
          </a>

        </div>
      </div>
      <!---->
      <div class="cellbox">
        <mt-cell title=""  to="//github.com" is-link>
          <i slot="icon" class="fa fa-envelope-o fa-fw"></i>
        </mt-cell>
      </div>
      <!---->
      <div class="cellbox">
        <mt-cell title=""  to="//github.com" is-link>
          <i slot="icon" class="fa fa-trophy fa-fw"></i>
        </mt-cell>
        <mt-cell title=""  to="//github.com" is-link>
          <i slot="icon" class="fa fa-shopping-cart fa-fw"></i>
        </mt-cell>
        <mt-cell title=""  to="//github.com" is-link>
          <i slot="icon" class="fa fa-folder-o fa-fw"></i>
        </mt-cell>
      </div>
      <!---->
      <div class="cellbox">
        <mt-cell  title=""  to="//github.com" is-link>
          <i slot="icon" class="fa fa-cog fa-fw"></i>
        </mt-cell>
        <mt-cell title="" value="" to="//github.com" is-link>
          <i class="fa fa-user-o fa-fw" slot="icon"></i>
        </mt-cell>
        <mt-cell title=""  to="//github.com" >
          <i class="fa fa-lightbulb-o fa-fw" slot="icon"></i>
          <mt-switch v-model="value"></mt-switch>
        </mt-cell>   
        <mt-cell title=""  to="//github.com" is-link>
          <i class="fa fa-clock-o fa-fw" slot="icon"></i>
        </mt-cell> 
        <mt-cell title=""  to="//github.com" is-link>
          <i class="fa fa-bell-o fa-fw" slot="icon"></i>
        </mt-cell>
        <mt-cell title=""  to="//github.com" is-link>
          <i slot="icon" class="fa fa-car fa-fw"></i>
        </mt-cell>                    
      </div>
      <!---->
      <div class="cellbox">
        <mt-cell title=""  to="//github.com" is-link>
          <i class="fa fa-share-square-o fa-fw" slot="icon"></i>
        </mt-cell>
        <mt-cell title=""  to="//github.com" is-link>
          <i class="mui-icon mui-icon-info" slot="icon"></i>
        </mt-cell>
      </div>
      <!---->
      <div class="drop bgwhite"></div>
    </div>
  </div>
</template>


<script>
import {Toast} from 'mint-ui';
export default {
  name: 'myzone',
  data() {
    return {
      checkInMsg: '',
      isCheckin: false
    }
  },
  methods: {
    checkin() {
      this.isCheckin = true;
      this.CheckInMsg = "";
      Toast({
        message: '',
        iconClass: 'fa fa-check fa-3x'
      });
    }
  }
}

</script>

<style scoped lang="scss">
  @import '../../sass/common';
  @import '../../sass/myzone';
</style>



